
.movieBox{
   background-color:#466E7A;
   margin-bottom: 60px;
   font-weight: 300;
   padding:20px;
   border-radius: 7px;
  display: grid;
  gap:15px;
  grid-template-columns:minmax(150px,1fr)2fr;
  text-decoration: none;
  color:inherit;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.movie__title{
   margin:0;
   font-weight: 300;
   text-decoration: none;
}
.movie__title a{
   color:white;
   margin-bottom: 5px;
   font-size: 20px;
   text-decoration: none;
   font-weight: 500;
}
.movie__img{
position: relative;
top:-50px;
max-width: 150px;
width: 100%;
margin-right: 30px;
box-shadow: 0 30px 60px -12px rgba(50, 50, 93, 0.25),
    0 18px 36px -18px rgba(0, 0, 0, 0.3), 0 -12px 36px -8px rgba(0, 0, 0, 0.025);
}
.movie__genres{
   list-style: none;
   padding:0;
   margin:0;
   display: flex;
   flex-wrap:wrap;
   margin:5px 0px;
}
.movie__genres li{
   margin-right:10px;
   margin-bottom:10px;
   font-size:14px;
   font-weight: 700;
}